import React from "react";
import styles from "./bottomNavigation.module.css";
import { Link } from "@reach/router";

const labelName = [
  { iconName: `${styles.index}`, itemText: "首页", path: "/test" },
  { iconName: `${styles.seeGoods}`, itemText: "识物", path: "/test/seeGoods" },
  { iconName: `${styles.classify}`, itemText: "分类", path: "/test/cateList" },
  { iconName: `${styles.shopping}`, itemText: "购物车", path: "/test/cat" },
  { iconName: `${styles.user}`, itemText: "个人", path: "/test/user" }
];
const bottomNavigation = () => {
  return (
    <div className={styles.bottomBar}>
      {labelName.map(({ itemText, iconName, path }) => (
        <React.Fragment key={itemText}>
          <Link to={path}>
            <div className={`${styles.icon} ${iconName}`} />
            <span className={styles.itemText}>{itemText}</span>
          </Link>
        </React.Fragment>
      ))}
    </div>
  );
};

export default bottomNavigation;



