import React, { Component } from "react";
import { Router } from "@reach/router";
import Loadable from "react-loadable";
import Loading from "loading/lottie";
import "antd-mobile/lib/list/style/index.css";
import "antd-mobile/lib/toast/style/index.css";
import NotFound from "./productList/NotFound";
import Footer from "./productList/bottomNavigation/bottomNavigation";
import styles from "./regesiterAntd.module.css";
import Header from "./productList/recommendList/home/header/header";
const headCateList = [
    { name: "推荐", id: 163 },
    { id: 1005000, name: "居家" },
    { id: 1008000, name: "鞋包配饰" },
    { id: 1010000, name: "服装" },
    { id: 1043000, name: "电器" },
    { id: 1013001, name: "洗护" },
    { id: 1005002, name: "饮食" },
    { id: 1005001, name: "餐厨" },
    { id: 1011000, name: "婴童" },
    { id: 1019000, name: "文体" },
    { id: 1065000, name: "特色区" }
];
const Home = Loadable({
    loader: () => import("./productList/recommendList/home/index"),
    loading: () => <Loading />
});
const User = Loadable({
    loader: () => import("./productList/user/loginVerification"),
    loading: () => <Loading />
});
const Category = Loadable({
    loader: () => import("./productList/recommendList/categoryList/categoryList"),
    loading: () => <Loading />
});
const DiscernList = Loadable({
    loader: () => import("./productList/discernList/index"),
    loading: () => <Loading />
});
const CateList = Loadable({
    loader: () => import("./productList/cateList/index"),
    loading: () => <Loading />
});
const Shopping = Loadable({
    loader: () => import("./productList/shoppingGrolley/index"),
    loading: () => <Loading />
});
const Detail = Loadable({
    loader: () => import("./productList/Detail/index"),
    loading: () => <Loading />
});
class Index extends Component {
    componentDidMount() {
        document.title = "网易严选 - 以严谨的态度，为中国消费者甄选天下优品";
    }
    render() {
        return (
            <div
                style={{
                    width: "100%",
                    backgroundColor: "#f4f4f4",
                    height: window.innerHeight
                }}
                id={styles.wrapper}
            >
                <Router basepath="/test" >
                    <Home path="/" url={"163"} />
                    <User path="user/*" />
                    <Category path="category/:urlId" url={"category?id="} />
                    <DiscernList path="seeGoods" url={"topic"} />
                    <CateList path="cateList" url={"cateList"} />
                    <Shopping path="cat" />
                    <Detail path="detail/*" />
                    <NotFound default />
                </Router>
                <Router primary={false}  basepath="/test">
                    <Header headCateList={headCateList} path="category/:id" />
                    <Header path="/" headCateList={headCateList} />
                    <Footer path="seeGoods" />
                    <Footer path="cat" />
                    <Footer path="cateList" />
                </Router>
            </div>
        );
    }
}

export default Index;



