import React, { PureComponent, Fragment } from "react";
/*import WrappedComponent from "./view/category";
import controller from "./Model/bufferAnimation";*/
import { Link } from "@reach/router";
import Swiper from "swiper/dist/js/swiper.min";
import styles from "./view/category.module.css";

class Category extends PureComponent {
  /*
  之前手动模拟的滑动组件用了swipe之后注释掉
  constructor() {
    super(...arguments);
    this.getElement = this.getElement.bind(this);
    this.initialDistance = this.initialDistance.bind(this);
    this.ref = createRef();
    this.getController = controller();
  }
  componentDidMount() {
    this.getController.reducerObservable$.next({
      element: this.ref.current,
      type: "GET_touchElement"
    });
    this.initialDistance(this.props.initialDistance);
  }
  componentWillUnmount() {
    this.getController.historyDistance$.unsubscribe();
    this.getController.reducerObservable$.unsubscribe();
    this.getController.getAnimationElement$.unsubscribe();
  }
  getElement() {
    this.getController.reducerObservable$.next({
      element: this.ref.current,
      type: "GET_touchElement"
    });
  }
  initialDistance(initialDistance) {
    this.getController.reducerObservable$.next({
      distance: initialDistance,
      type: "INITIAL_distance"
    });
  }*/
  swipe = null;
  state = {
    active: this.props.activeStatus
  };
  componentDidMount() {
    this.swipe = new Swiper(`.${styles.List}`, {
      freeMode: true,
      slidesPerView: "auto",
      slideToClickedSlide: true,
        preventClicks : false
    });
  }
  componentWillUnmount() {
    this.swipe.destroy();
  }
  render() {
    const { headCateList } = this.props;
    return (
      <div className={styles.List + " swiper-container"}>
        <div className={` swiper-wrapper`}>
          {headCateList.map(({ name, id }) => (
            <Fragment key={id}>
              <div
                className={`swiper-slide ${styles.Item}`}
                onClick={() => this.setState({ active: name })}
              >
                <Link
                  to={`/test${id !== 163 ? `/category/${id}` : "/"}`}
                  style={{
                    color: this.state.active === name ? "#b4282d" : "#333"
                  }}
                >
                  {name}
                </Link>
              </div>
            </Fragment>
          ))}
        </div>
      </div>
    );
  }
}

export default Category;



