import React,{PureComponent} from "react";
import styles from "./header.module.css";
import Icon from "icon/createIcon";
import Category from "./categoryList/index";
import Footer from "../../../bottomNavigation/bottomNavigation";

export default class Header extends PureComponent {
  render(){
    const {headCateList} =this.props;
      return (
          <div className={styles.headerPlaceholder}>
              <div className={styles.header}>
                  <div className={styles.line}>
                      <div className={styles.logo} />
                      <div className={styles.search}>
                          <Icon name="search" style={styles.icon} />
                          <span>搜索商品, 共9835款好物</span>
                      </div>
                  </div>
                  <Category
                      activeStatus="推荐"
                      startDistance={0}
                      initialDistance={0}
                      headCateList={headCateList}
                  />
              </div>
              <Footer/>
          </div>
      );
  }
}


