import React from 'react';

 const CreateIcon = (props) => {
    const {name,style} =props;
    if(style){
      return <i className={`iconfont icon-${name} ${style}`} />;
    }else {
      return <i className={`iconfont icon-${name}`} />;
    }

 };
 export default CreateIcon;



