import React,{Component} from 'react';
import Lottie from 'react-lottie';
import animationData from '../empty_status.json';

export default class Loading extends  Component{
    state={
        stop:false
    };
    componentWillUnmount(){
        this.setState({
            stop:true
        })
    }
    render(){
        return <div
            style={{
                width:'100%',
                height:window.innerHeight,
                display:'flex',
                flexDirection:'row',
                alignItems:'center',
                backgroundColor:'#fff'
            }}
        >
            <Lottie
                options= {
                    {
                        loop: true,
                        autoplay: true,
                        animationData: animationData,
                        renderer: 'canvas'
                    }
                }
                style={
                    {
                        width:'100%',
                    }
                }
                isStopped={this.state.stop}
            />
        </div>
    }
}


